}
.togglebutton{
    display: none;
}
@media screen and (max-width: 600px) {
    .navblock2 ul{
        display: none;
    }
    .togglebutton{
        display: block;
        position: absolute;
        background: #101D28;
        padding: 6px;
        padding-left: 20px;
        padding-right: 20px;
        right: 0;
        top: 17%;
        border: none;
        border-left: solid 4px #0d6efd;
        color: rgb(255, 255, 255);
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;
    }
  }
