}
.textBox{
    z-index: 3;
    width: fit-content;
    border-radius: 32px;
    border: solid 12px #bbddfa;
    max-width: 100%;
    padding: 30px;
    background-image: url(./Images/anibg.svg);
    background-size: cover;
    background-repeat: repeat-x;
    animation: animatedBackground 6s linear infinite ;
    font-size: 48px;
}
@keyframes animatedBackground {
  from {background-position: center;}
to {background-position: -1000px center;}
}
.bg-grad{
    background: linear-gradient(-45deg, #00c3ffce, #ebf2f870, #52e5f070);
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.carText{
    max-width: 67.8%;
    top: 55%;
    position: absolute;
    transform: translateY(-50%);
    
}
.undertext{
    color: rgb(68, 68, 68);
    margin-top: 20px;
    font-size: 22px;
    padding-left: 32px;
    padding-right: 32px;
}
.leftline{
    background: wheat;
    height: 3px;
    position: absolute;
    top: 40%;
    width: 8.3333%;
}
.rightline{
    background: wheat;
    height: 3px;
    position: absolute;
    top: 40%;
    width: 24%;
    animation-name: fillline;
    animation-duration: 4s;
    animation-timing-function: ease-out; 
  animation-delay: 0;
}

@keyframes fillline {
    0% {
        width: 0;        
    }
      100% {
        width: 100;
      }
}
