@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-200px * 4.62))}
}
.slidecontainer{
    overflow: hidden;
}
.slider {
	background: white;
	height: 250px;
	margin: 0;
	overflow:hidden;
	position: relative;
}
.slide-track {
    width: 50%;
    animation: scroll 20s linear infinite;
    display: flex;
}	

#carInd{
    transform: translateY(7vh);
}
.multiItemSlider{
    min-height: 60vh;
}

.multiItemSlider .carousel-inner .card{
    height: 55vh ;
    border-radius: 0 !important;
    text-align: left;
    background-size: cover;
    background-position: center;
    background: url(./Images/multislidebg1.png);
    border: none;
}
.multiItemSlider .carousel-inner .card:hover{
    filter: brightness(1.5);
}
.multiItemSlider .carousel-inner .card .card-body{
    padding: 10%;
    background: #000000b9;
    color: white;
}
.multiItemSlider .carousel-inner .card #btn{
    border-radius: 8px;
    padding: 4px;
    padding-left: 16px;
    padding-right: 16px;
    border: none;
    color: white;
background: linear-gradient(90deg, #0D2E4B 0%, #1F5F96 100%);
}
.multiItemSlider .carousel-inner .card .btn-primary{
    border-radius: 8px;
    padding: 4px;
    padding-left: 16px;
    padding-right: 16px;
    border: none;
    color: white;
    background: rgba(255, 255, 255, 0.15);
}
.carousel-dark .carousel-indicators [data-bs-target] {
    background-color: #fff;
}
.carousel-indicators [data-bs-target] {
    width: 10px !important;
    height: 10px !important;
    border-radius: 100%;
}
